






















































import { computed, defineComponent, PropType } from '@vue/composition-api';
import Icon from '@/app/editor/client/view/icon';
import useI18N from '@/app/shared/client/logic/i18n';
import { MenuData } from '@/app/shared/client/type/menu';
import {
  MenuContentEditorActionPayload,
  MenuContentEditorActionType,
} from '@/app/editor/client/type/editor-action/menu-content-editor';
import { isNotNullOrEmpty } from '@/app/shared/client/helper/base';

interface MenuContentEditorProps {
  label: string;
  value: MenuData | undefined;
}

function useEditorNavigateHandler(
  emit: (event: string, payload: MenuContentEditorActionPayload) => void,
): () => void {
  function navigateToEditor(): void {
    emit('editor-action', {
      type: MenuContentEditorActionType.NAVIGATE_TO_EDITOR,
    });
  }

  return navigateToEditor;
}

function useMenuItemAddHandler(
  emit: (event: string, payload: MenuContentEditorActionPayload) => void,
): () => void {
  function addMenuItem(): void {
    emit('editor-action', {
      type: MenuContentEditorActionType.ADD_MENU_ITEM,
    });
  }

  return addMenuItem;
}

const MenuContentEditor = defineComponent({
  components: {
    ArrowRightIcon: Icon.ArrowRightIcon,
    PlusIcon: Icon.PlusIcon,
  },
  props: {
    label: {
      required: true,
      type: String,
    },
    value: {
      required: false,
      default: undefined,
      type: Object as PropType<MenuData>,
    },
  },
  setup(props: MenuContentEditorProps, { root, emit }) {
    const i18n = useI18N(root.$store);

    const menuItemList = computed(() => {
      const items = props.value?.items ?? [];
      return items.filter((item) => isNotNullOrEmpty(item.title));
    });
    const atLeastOneMenuItem = computed(() => menuItemList.value.length > 0);
    const menuButtonTextList = computed(() => menuItemList.value.map((item) => item.title));

    const navigateToEditor = useEditorNavigateHandler(emit);
    const addMenuItem = useMenuItemAddHandler(emit);

    return {
      i18n,
      menuButtonTextList,
      atLeastOneMenuItem,
      navigateToEditor,
      addMenuItem,
    };
  },
});

export default MenuContentEditor;
